import { __ } from '@wordpress/i18n';
import { useBlockProps } from '@wordpress/block-editor';
import classnames from 'classnames';
import './editor.scss';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import 'bootstrap/dist/css/bootstrap.css';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

/**
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 * @since 0.1.0
 *
 * @returns {WPElement} Element to render.
 */

function Edit(props) {
	const {
		displayPublicationDate,
		displayUpdateDate,
		displayAuthor
	} = props.attributes;


	const blockProps = useBlockProps({
		className: classnames({
			ptqblock__list: true,
			'has-dates': displayPublicationDate || displayUpdateDate,
			'has-author': displayAuthor,
		}),
	});
	return (
		<div {...blockProps}>
			<Form id="raviol-form" style={{ maxWidth: '650px' }}>
				<Row>
					<Col>
						<FloatingLabel controlId="floatingInput" label="First name" className="mb-4">
								<Form.Control type="text" placeholder="First name" />
						</FloatingLabel>
					</Col>
					<Col>
						<FloatingLabel controlId="floatingInput2" label="Last name" className="mb-4">
							<Form.Control type="text" placeholder="Last name" />
						</FloatingLabel>
					</Col>
				</Row>
				<Row>
					<Col>
						<FloatingLabel controlId="floatingInput3" label="Subject" className="mb-4">
								<Form.Control type="text" placeholder="Subject" />
						</FloatingLabel>
					</Col>
					<Col>
						<FloatingLabel controlId="floatingInput4" label="Email" className="mb-4">
							<Form.Control type="email" placeholder="Email" />
						</FloatingLabel>
					</Col>
				</Row>
				<div className="form-floating" className="mb-4 position-relative">
				<div className="textarea-before"></div>
					<textarea className="form-control" placeholder="Message" id="floatingTextarea2" style={{ height: '100px' }}></textarea>
				</div>
				<Row>
				<Col className="col-12">
					<button className="btn btn-lg btn-secondary fs-6 rounded-0 p-3 px-4 mb-2 pe-none">Submit a request</button>
				</Col>	
				<Col className="col-12">
					<small class="small text-muted pe-none">By clicking "Submit Request" you agree to receive marketing communications</small>
				</Col>
				</Row>
			</Form>
		</div>
	);
}

export default Edit;
